
.profile__wrapper{
    display: flex;
    flex-direction: column;
    padding: 0 70px 0 70px;
}

.profile{
    display: flex;
    flex-direction: column;
    width: 400px;
    align-self: center;
}

.profile__welcome{
    font-family: Inter;
    font-size: 24px;
    font-style: normal;
    font-weight: 500;
    line-height: 29px;
    letter-spacing: 0em;
    text-align: center;
    color: #FFFFFF;
    margin: 75px 0 100px 0;
}

.profile__link{
    text-decoration: none;
    transition: 0.5s;
    color: white;
}

.profile__link:hover{
    cursor: pointer;
    opacity: 0.6;
}

.profile__input{    
    border: none;
    font-family: 'Inter', Arial, sans-serif;
    font-weight: 400;
    font-size: 11px;
    line-height: 13px;
    text-align: right;
    color: #CCCCCC;
    background-color: transparent;    
    display: flex;
    flex-direction: row;
}

.profile__form{
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 20px;    
}

.profile__section{
    width: 100%;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
}

.profile__label{
    font-family: 'Inter', Arial, sans-serif;
    font-weight: 500;
    font-size: 11px;
    line-height: 13px;
    color: #FFFFFF;
}

.profile__register{
    display: flex;
    flex-direction: row;
    gap: 6px;
}

.profile__link-paragraph{
    margin: 0;
}

.profile__link{
    text-decoration: none;
    color: #EE3465;
    font-family: 'Inter', Arial, sans-serif;
    font-weight: 400;
    font-size: 14px;
    line-height: 17px;
}

.profile__not-register{
    margin: 0;
    color: #8B8B8B;
    font-family: 'Inter', Arial, sans-serif;
    font-weight: 400;
    font-size: 14px;
    line-height: 17px;
}

@media screen and (max-width: 1023px){
    .profile{    
        margin-top: 232px;
    }
}

@media screen and (max-width: 767px){
    .profile{    
        margin-top: 150px;
    }
}


@media screen and (max-width: 424px){
    .profile{    
        margin-top: 56px;
        align-items: center
    }

    .profile__welcome{        
        letter-spacing: 0em;
        text-align: center;
        
        margin: 50px 0 80px 0;
    }

    .profile__link{        
        font-size: 12px;
        line-height: 16px;        
    }
    
    .profile__not-register{
        font-size: 12px;
        line-height: 16px;
    }
}

.profile__form .profile__section:first-child{
    padding-bottom: 20px;
    border-bottom: 1px solid #424242;
}

@media screen and (max-width: 1023px){
    .profile__wrapper{        
        padding: 0 30px 0 30px;
    }
}

@media screen and (max-width: 767px){
    .profile__wrapper{        
        padding: 0 20px 0 20px;
    }
}

@media screen and (max-width: 424px){
    .profile__wrapper{        
        padding: 0 10px 0 10px;
    }
}
