.popup{
    position: fixed;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    color: #000000;
    visibility: hidden;
    opacity: 0;
    transition: visibility 0.5s, opacity 0.5s linear;
    display: flex;
    justify-content: flex-end;
    align-items: center;
    
}

.popup_opened{
    visibility: visible;
    opacity: 1;
}

.popup__overlay{
    position: absolute;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    background-color: rgba(0, 0, 0, .5);
}

.popup__container{
    width: 100%;
    max-width: 520px;
    min-width: 320px;
    z-index: 1;
    background-color: #202020;
    box-shadow: 0px 0px 25px rgba(0, 0, 0, 0.15);
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    position: relative;
    height: 100%;
}

.popup__close-button{
    position: absolute;
    top: 23px;
    right: 23px;
}

.popup__title{
    color: white;
    font-family: 'Inter', Arial, sans-serif;
    font-style: normal;
    font-weight: 500;
    font-size: 18px;
    line-height: 22px;
    padding-bottom: 28px;
}